@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;700&display=swap');

:root{
    --text-color: #cd5ff8;
    --h4-color: #0151cc;
    --navbar-bg: #1c0426;
    --sec-clr: #fff;
    --navbar-color: #fff;
    --navbar-box-shadow: white;
    --box-color: rgb(51, 51, 51);
    --shadow-color-: rgb(201, 201, 201);
}
::-webkit-scrollbar {
    width: 6px; 
    background: #1c0426; 
  }
  
  ::-webkit-scrollbar-track {
    background: #f1f1f1; /* Track color */
  }
  
  ::-webkit-scrollbar-thumb {
    background: #cd5ff8; /* Thumb color */
  }
  
  ::-webkit-scrollbar-thumb:hover {
    background: #ac38d1; /* Thumb color on hover */
  }
  
  

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

html {
    scroll-behavior: smooth;
    overflow-x: hidden;

}

body {
    overflow-x: hidden;
    font-family: 'Poppins', sans-serif;
    font-size: 16px;
    line-height: 1.5;
    color: var(--text-color);
    
}

img {
    max-width: 100%;
    /* margin-top: 20px; */
}


h4 {
    text-align: center;
    margin-top: 45px;
    margin-bottom: 30px;
    color: var(--h4-color);

}


h1,
h2 {
    margin-bottom: 15px;
}

h2{
    font-size: 40px !important;
}

.container {
    max-width: 1100px;
    margin: 0 auto;
    padding: 0 30px;
    width: 100%;
}

span {
    color: var(--text-color);
}


@media(max-width: 670px) {
    .boxes .container {
        text-align: center;
    }

    .box h2 {
        font-size: 25px;
    }

    .header .container {
        margin: 10px;
    }
}

@media(max-width: 600px) {
    .header .container h1 {
        font-size: 28px;
    }
}

h2 {
    font-size: 3rem;
    margin: 30px;
    color: var(--navbar-box-shadow);
}

.pra p {
    margin: 35px;
}

.lower h3 a {
    text-decoration: none;
    font-size: 30px;
    color: var(--navbar-box-shadow);
}

.lower a {
    text-decoration: none;
    color: var(--navbar-box-shadow);
    font-weight: bold;
    /* font-size: 12px; */

}

.lower i {
    color: var(--navbar-bg);

}

.lower hr {
    width: 94%;
    color: var(--navbar-bg);
    border-color: var(--navbar-bg);
    margin: 25px;
}


.contact {
    background: transparent !important;
    color: var(--navbar-box-shadow);
    min-height: 100vh;
    padding: 50px 0;
    display: flex;
    align-items: center;
}

.contact h2 {
    font-size: 3rem;
    margin-bottom: 30px;
}

form {
    max-width: 500px;
    margin: 0 auto;
    text-align: center;
}

.form-group {
    margin-bottom: 20px;
    text-align: left;
}

label {
    display: block;
    margin-bottom: 10px;
}

input,
textarea {
    width: 100%;
    padding: 10px;
    border: none;
    border-radius: 5px;
    font-size: 1.2rem;
    background: var(--sec-clr);
    color: var(--navbar-bg);

}

textarea {
    resize: vertical;
    min-height: 100px;
}

button[type="submit"] {
    display: block;
    background-color: transparent;
    color: white;
    padding: 10px 20px;
    border: 1px solid white;
    border-radius: 5px;
    text-decoration: none;
    font-size: 1.2rem;
    margin-top: 20px;
    cursor: pointer;
    transition: all 0.3s ease;
}

button[type="submit"]:hover {
    background-color: var(--text-color);
    color: white;
    border: 1px solid var(--text-color);
}



@media(max-width: 405px) {

    img {
        width: 200px;
    }

    h2 {
        font-size: 1.7rem;
    }

    .contact h2 {
        font-size: 1.7rem;
    }
}

section.project {
    text-align: center;
    padding: 20px;
    height: fit-content;
    padding: 50px 15px;
}

.projects {
    display: grid;
    gap: 10px;
}

.project-container {
    position: relative;
    overflow: hidden;
    width: 100%;
    margin: 0;
}

.project{
    margin-top: 40px;
}

.project-overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(255, 0, 0, 0.5); /* Red background with 50% opacity */
    z-index: 1; /* Place the overlay above the image */
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    opacity: 0;
    transition: opacity 0.3s;
}
.project-overlay a{
    color: white;
    text-decoration: none;
}

.project-overlay a:hover{
    text-decoration: underline;
}
.project-text {
    color: #fff;
    text-decoration: none;
    font-weight: bold;
    font-size: 16px;
    margin-top: 10px;
}

.project-image {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: transform 0.3s;
    z-index: 0; /* Ensure the image is behind the overlay */
}

.project-container:hover .project-overlay {
    opacity: 1;
}

.project-container:hover .project-image {
    transform: scale(1.1);
}

@media (min-width: 1200px) {
    .projects {
        grid-template-columns: repeat(3, 1fr);
    }
}

@media (max-width: 1199px) and (min-width: 769px) {
    .projects {
        grid-template-columns: repeat(2, 1fr);
    }
}

@media (max-width: 768px) {
    .projects {
        grid-template-columns: 1fr;
    }

}